import { Injectable } from '@angular/core'


@Injectable()
export class Layout {
  public isSlide:boolean = false;
  public cuTitle = {
    title: '',
    page: '',
  };
  public submenus = {
    sitemap: {title: ' ',
      menus: [' '],
      urls:   ['/sitemap'],
    },
    dmhc: {title: '',
      menus: [' ', '', ''],
      urls:   ['', '/dmhc/process',  '', ],
    },
    lecture: {title: ' ',
      menus:  ['  ', '  ', ' ', ' '],
      urls:   ['/lecture/all', '/lecture/new', '/lecture/edit', ''],
    },
    survey:  {title: ' ',
      menus: ['  ', '  ', '  '],
      urls:   ['/survey/new', '/survey/edit', '', ],
    },
    assign: {title: ' ',
      menus:  ['  ', '  ', '  ', 'Hash Tag '],
      urls:   ['/assign/all', '/assign/new', '/assign/edit', '/assign/hash',],
    },
    manage: {title: ' ',
      menus:  ['  ', '  ', '  '],
      urls:   ['/manage/all', '/manage/detail', '', ],
    },
    mypage:  {title: 'My Page',
      menus: ['  '],
      urls:   ['/mypage/user', ],
    },
    guest:  {title: '',
      menus: ['', ' ', ' '],
      urls:   ['/guest/notice', '/guest/faq', '/guest/qna',],
    }
  }
  public currentMenu = {title:'', menus: [], urls: []};
  public currentPage = '';


  public values = {
    aside_width: "0",
    aside_padding: "0",
  };


  public asideView() {
    this.isSlide = false;
    setTimeout(()=>{
      this.isSlide = true;
    }
    ,10)
  }
  public asideHide() {
    this.isSlide = false;
  }
}



