import { AbstractControl } from '@angular/forms';
import { Injectable } from '@angular/core';



@Injectable()
export class ValidatorMSG {
  value: string;
  dirty: string;
  invalid: string;
  valid: string;
  invalidMin: string;
  invalidMax: string;
  invalidPattern: string;
  require: string;


  constructor() {
    this.value= '';
    this.dirty= '';
    this.invalid= '';
    this.valid= '';
    this.invalidMin= '';
    this.invalidMax= '';
    this.invalidPattern= '';
    this.require= '  !';
  }
}

@Injectable()
export class LectureRegexValidators {
  titleMin:number = 1;
  titleMax:number = 30;
};


@Injectable()
export class LectureValidMsgs {

  titleLabel: ValidatorMSG;

  constructor(private valider: LectureRegexValidators)
  {
    this.titleLabel = new ValidatorMSG();

    this.titleLabel.value = '  .';
    this.titleLabel.invalidMin = "  ! (" + this.valider.titleMin + " )";
    this.titleLabel.invalidMax = "  ! (" + this.valider.titleMax + " )";
    this.titleLabel.invalid = "   .";
  }
}



