import { Injectable } from '@angular/core'
import { AbstractControl, FormGroup, Validators, FormBuilder } from '@angular/forms';

@Injectable()
export class NewHashData {
    hashForm: FormGroup
    defaultPageNum:number = 1;
    constructor(
      private fb: FormBuilder,
      // private valider: HashRegexValidators,
    ) {
          this.hashForm= this.fb.group({});
    }
}


export class ValidatorMSG {
  value: string;
  dirty: string;
  invalid: string;
  valid: string;
  invalidMin: string;
  invalidMax: string;
  invalidPattern: string;
  require: string;


  constructor() {
    this.value= '';
    this.dirty= '';
    this.invalid= '';
    this.valid= '';
    this.invalidMin= '';
    this.invalidMax= '';
    this.invalidPattern= '';
    this.require= '  !';
  }
}

export class HashRegexValidators {
  titleMin:number = 1;
  titleMax:number = 30;
};


export class HashValidMsgs {

  titleLabel: ValidatorMSG;

  constructor(private valider: HashRegexValidators)
  {
    this.titleLabel = new ValidatorMSG();

    this.titleLabel.value = '  .';
    this.titleLabel.invalidMin = "  ! (" + this.valider.titleMin + " )";
    this.titleLabel.invalidMax = "  ! (" + this.valider.titleMax + " )";
    this.titleLabel.invalid = "   .";
  }
}



